*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:lightslategray;
}
.app-container{
    width: 400px;
    background-color: white;
    text-align: center;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
}
.heading{
    color: orangered;
    font-weight: 600;
    margin-bottom: 15px;
    font-size: 20px;
}
.time{
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 15px;
}
.date{
    font-size: 20px;
    color: #888;
}